@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
*,*:before,*:after{box-sizing:border-box}body{font-family:'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;font-weight:500;font-size:18px;line-height:28px;color:#333;margin:0;background-color:#fff;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html,body{height:100%;margin:0}input,button,textarea,select{resize:none;border-radius:0;font-family:'Roboto', sans-serif;line-height:28px}button{cursor:pointer}h1,h2,h3,h4{font-weight:bold}a{transition:color .2s}ul{padding:0}ul li{list-style:none}p{margin:0;padding:0}figure{margin:0;padding:0}

.main{grid-area:main;display:flex;flex-direction:column;margin:0 140px}.main__title{font-size:24px;line-height:44px;color:#333;font-weight:500;margin-bottom:40px;margin-top:0}.main__title strong{font-weight:bold;display:block;font-size:32px}.error{display:block;margin:0 auto;width:50%}.SearchPanel+.Jokes__item.Loader{display:none}@media (max-width: 1200px){.main{margin:0 70px}}@media (max-width: 1000px){.main{margin:0 40px}}@media (max-width: 750px){.main{margin:0 20px}}

.SearchPanel{display:flex;flex-direction:column;margin-bottom:40px}.SearchPanel__item{display:flex;flex-wrap:wrap;position:relative;align-items:center;margin-bottom:10px}.SearchPanel__item label{font-size:18px;line-height:26px;font-weight:normal;padding-left:35px}.SearchPanel__button{width:-webkit-max-content;width:-moz-max-content;width:max-content;background:linear-gradient(90deg, #8EA7FF 0%, #7291FF 100%);border-radius:10px;border:none;margin-top:10px;display:flex;padding:0;transition:all .2s ease-in}.SearchPanel__button span{font-size:16px;line-height:1.375;color:#fff;font-weight:bold;margin:10px 40px}.SearchPanel__button:focus{outline:none;box-shadow:1px 1px 0 rgba(0,47,216,0.6),-1px 1px 0 rgba(0,47,216,0.6),1px -1px 0 rgba(0,47,216,0.6),-1px -1px 0 rgba(0,47,216,0.6)}.SearchPanel__button:hover{box-shadow:0 4px 6px rgba(0,0,0,0.3)}.SearchPanel__button:active{-webkit-transform:translate(1px, 3px);transform:translate(1px, 3px)}.SearchPanel__input{height:20px;width:20px;margin:0;opacity:0;position:absolute;top:2px;left:0}.SearchPanel__input+label::before{content:url("data:image/svg+xml,%3Csvg width='20' height='20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='10' cy='10' r='9' stroke='%23ABABAB' stroke-width='2'/%3E%3C/svg%3E");position:absolute;top:2px;left:0}.SearchPanel__input+label::after{content:url("data:image/svg+xml,%3Csvg width='20' height='20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='10' cy='10' r='9' stroke='%23333' stroke-width='2'/%3E%3Ccircle cx='10' cy='10' r='5' fill='%23333'/%3E%3C/svg%3E");opacity:0;position:absolute;left:0;top:2px;cursor:pointer;transition:opacity .2s ease-in-out}.SearchPanel__input:checked+label::after{opacity:1}.SearchPanel__input:focus+label::before{box-shadow:0 0 0 2px #7291FF;outline:2px solid transparent;height:20px}.SearchPanel__search{flex-basis:100%;border:2px solid #333;border-radius:10px;font-size:16px;line-height:1.375;padding:10px 15px}.SearchPanel__search::-webkit-input-placeholder{color:#ABABAB}.SearchPanel__search:-ms-input-placeholder{color:#ABABAB}.SearchPanel__search::-ms-input-placeholder{color:#ABABAB}.SearchPanel__search::placeholder{color:#ABABAB}.SearchPanel__categories{flex-basis:100%;flex-wrap:wrap}.SearchPanel__categories,.SearchPanel__search{position:absolute;clip:rect(0 0 0 0);width:1px;height:1px;margin:-1px}.SearchPanel__categories li{margin-right:10px;margin-bottom:10px;border:2px solid #F8F8F8;border-radius:6px;position:relative}.SearchPanel__categories li:last-child{margin-right:0}.SearchPanel__categories label{color:#ABABAB;padding:6px 15px;font-weight:500;font-size:12px;line-height:16px;letter-spacing:2px;text-transform:uppercase;display:block;transition:all .2s ease-in-out}.SearchPanel__checkbox{opacity:0;position:absolute;height:100%;width:100%;z-index:1;margin:0;cursor:pointer}.SearchPanel__checkbox:checked+label{background:#F8F8F8;color:#333}.inputError{color:#c00}#categories:checked ~ .SearchPanel__categories{display:flex;position:relative;clip:unset;width:unset;height:auto;margin:20px 0 0 0}#search:checked ~ .SearchPanel__search{position:relative;clip:unset;width:unset;height:auto;margin:20px 0 0 0}

.Jokes{height:49vh;overflow:auto;position:relative}.Jokes__list{display:flex;flex-direction:column;margin-top:0;flex-basis:40vh;overflow:visible}.Jokes__item{background:#F8F8F8;border-radius:20px;max-width:900px;height:auto}.Jokes__item svg.heart{right:40px;top:40px}.Jokes__item svg .circle{fill:#fff}.Jokes__item .JokeItem__wrapper{margin:67px 40px 45px}.Jokes__item .JokeItem__text{margin-bottom:28px}@media (max-width: 750px){.Jokes{height:38vh}}

.JokeItem{position:relative;margin-bottom:20px}.JokeItem:last-child{margin-bottom:60px}.JokeItem__wrapper{display:flex;align-items:flex-start;margin:47px 20px 20px 20px}.JokeItem__icons{margin-right:20px}.JokeItem__icons svg.heart{position:absolute;cursor:pointer}.JokeItem__text-wrapper{display:flex;flex-direction:column;align-items:flex-start}.JokeItem__link{display:flex;align-items:flex-start;font-weight:500;font-size:10px;line-height:14px;cursor:pointer}.JokeItem__link:hover a{color:#5b7fff}.JokeItem__link:hover svg{-webkit-transform:translate(2px, -2px);transform:translate(2px, -2px);fill:#5b7fff}.JokeItem__link span{color:#ABABAB;margin-right:2px}.JokeItem__link a{margin-right:5px;color:#8EA7FF;transition:all .2s ease-in}.JokeItem__link svg{transition:all .2s ease-in}.JokeItem__info{font-weight:normal;font-size:10px;line-height:14px;color:#ABABAB}.JokeItem__text{color:#333;font-size:14px;line-height:20px;font-weight:normal;margin:5px 0 20px 0;word-break:break-word}.JokeItem__info mark{background:none;font-weight:500;color:currentColor}.JokeItem__info-wrapper{display:flex;align-items:center;justify-content:space-between;width:100%}.JokeItem__category{width:-webkit-max-content;width:-moz-max-content;width:max-content;background:#fff;border-radius:6px;display:flex}.JokeItem__category span{font-weight:500;font-size:10px;line-height:14px;letter-spacing:2px;text-transform:uppercase;padding:6px 20px}@media (max-width: 750px){.Jokes__item .JokeItem__wrapper{margin:47px 20px 20px}.JokeItem__info-wrapper{flex-wrap:wrap}}@media (max-width: 500px){.JokeItem__info{flex-basis:100%;margin-bottom:10px}.Jokes__item svg.heart{right:20px;top:20px}.JokeItem__link a{word-break:break-all;width:90%}}

.Jokes__item.Loader{height:200px}

.Header{display:flex;justify-content:space-between;align-items:center;height:auto;grid-area:header;margin:40px 140px 78px}.Header__logo{color:#333;font-size:20px;line-height:1.4;margin:0}.Header__menu{border:none;background:none;display:flex;justify-content:center;align-items:center;visibility:hidden}.Header__menu span{margin-left:10px;font-size:20px;line-height:1.4;color:#ABABAB}@media (max-width: 1200px){.Header{margin:40px 70px 78px}}@media (max-width: 1000px){.Header{margin:40px 40px 78px}}@media (max-width: 920px){.Header__menu{visibility:visible;z-index:2}.Header-overlay{position:relative}.Header-overlay:after{position:absolute;content:'';width:100vw;height:200vh;right:0;z-index:1;background:rgba(0,0,0,0.3)}}@media (max-width: 750px){.Header{margin:20px 20px 78px}.Header:after{display:none}}

.Footer{grid-area:footer;position:absolute;bottom:20px;left:0;width:-webkit-max-content;width:-moz-max-content;width:max-content;background:rgba(248,248,248,0.8)}.Footer__text{color:#ABABAB;font-weight:300;font-size:16px;line-height:1.4;padding:10px 20px}

.Favorite{grid-area:favourites;background:#F8F8F8;display:flex;flex-direction:column;height:auto}.Favorite_title{font-weight:500;font-size:20px;line-height:28px;color:#ABABAB;margin:40px 40px 20px}.Favorite_list{display:flex;flex-direction:column;margin:0 40px 0;overflow-y:auto}.Favorite_item{background:#fff;border-radius:10px;box-shadow:0 4px 6px rgba(0,0,0,0.1);max-height:-webkit-max-content;max-height:-moz-max-content;max-height:max-content;width:auto;min-width:360px}.Favorite_item svg.color{fill:#F8F8F8}.Favorite_item svg.heart{right:20px;top:20px}.Favorite_item svg .circle{fill:#F8F8F8}.Favorite_item .JokeItem__category{display:none}@media (max-width: 920px){.Favorite_title{display:none}.Favorite{padding-top:88px;position:absolute;width:60vw;height:100%;top:0;right:0;overflow:auto;z-index:1}.Favorite--open{display:none}.Favorite::after{position:fixed;content:'';top:0;right:0;opacity:.8;background:linear-gradient(to bottom, #F8F8F8 90%, rgba(248,248,248,0.1));width:inherit;height:88px}.Favorite_list{overflow-y:unset}}@media (max-width: 750px){.Favorite{width:100vw}.Favorite_list{margin:0 20px 20px}.Favorite_item{min-width:unset}}

.App{display:grid;grid-template-areas:'header favourites' 'main favourites' 'footer favourites';grid-template-rows:-webkit-max-content 1fr -webkit-max-content;grid-template-rows:max-content 1fr max-content;grid-template-columns:2fr 1fr;height:auto;min-height:100vh;position:relative;overflow:hidden}@media (max-width: 920px){.App{grid-template-areas:'header' 'main' 'footer';grid-template-columns:1fr}}@media (max-width: 350px){.App{min-height:110vh}}

